/**
* CSS file to hold styles that apply across the application
*/

@import url("https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap");

* {
  word-wrap: balance;
}

html,
body {
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

div#main {
  display: flex;
  flex-direction: column;
  width: 100vw;
}

section {
  position: relative;
  width: 100vw;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  height: 100%;
  width: 100vmax;
  object-fit: cover;
  pointer-events: none;
  transform: translate(-50%, 0);
}

h1 {
  font-family: "Cormorant Garamond", serif;
  margin: 0;
  line-height: 0.8;
  font-weight: 400;
  font-size: 7rem;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-weight: 400;
  font-size: 1.25rem;
}

p {
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
  margin-bottom: 0;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}


.container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));

  margin: 20px auto;
  padding: 50px;
  background-image: url("./images/2.jpg"); 
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.container .box {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 24px 16px;
  border-radius: 10px;
  border: 1px solid rgb(204, 204, 204);
  color:snow;
  text-align: center;
}

img.socialIcon {
  height: 30px;
  width: 30px;
}
